<template>
<div class="form-style-8">
  <h2>Login to your account</h2>
  <form id="events">
    <div class="event" v-for="(event, index) in events" :key="index">
      <input reqired v-model="events[index].title.ru" type="text" name="title" :placeholder="labels.title[$i18n.locale]" />
      <input v-model="events[index].image.ru" type="url" name="image" :placeholder="labels.image[$i18n.locale]" />
      <input v-model="events[index].source.ru" type="url" name="source" :placeholder="labels.source[$i18n.locale]" />
      <input reqired v-model="events[index].date" type="text" name="date" :placeholder="labels.date[$i18n.locale]" />
      <textarea reqired v-model="events[index].description.ru" :placeholder="labels.description[$i18n.locale]" @keydown="adjustTextarea"></textarea>
    </div>
    <input type="button" :value="labels.add[$i18n.locale]" @click="onAdd" />
    <input type="button" :value="labels.save[$i18n.locale]" @click="onSave" />
  </form>
</div>

</template>

<script>
import autosize from 'autosize';

export default {
  data() {
    return {
      labels: {
        title: {
          en: 'Title',
          ru: 'Заголовок',
          by: 'Загаловак',
        },
        description: {
          en: 'Description',
          ru: 'Описание',
          by: 'Апісанне',
        },
        add: {
          en: 'Add',
          ru: 'Добавить',
          by: 'Дадаць',
        },
        save: {
          en: 'Save',
          ru: 'Сохранить',
          by: 'Захаваць',
        },
        position: {
          en: 'Position',
          ru: 'Должность',
          by: 'Пасада',
        },
        date: {
          en: 'Date',
          ru: 'Дата',
          by: 'Дата',
        },
        event: {
          en: 'Event',
          ru: 'Событие',
          by: 'Падзея',
        },
        image: {
          en: 'Image',
          ru: 'Картинка',
          by: 'Малюнак',
        },
        initials: {
          en: 'Full name',
          ru: 'ФИО',
          by: 'Прозвішча',
        },
        source: {
          en: 'Source',
          ru: 'Источник',
          by: 'Крыніца',
        },
      },
      title: {
        en: '',
        ru: '',
        by: '',
      },
      position: {
        en: '',
        ru: '',
        by: '',
      },
      events: [
        {
          date: '',
          title: { en: '', ru: '', by: '' },
          description: { en: '', ru: '', by: '' },
          image: '',
          source: '',
        },
      ],
    };
  },
  methods: {
    onAdd() {
      const event = {
        date: '',
        title: { en: '', ru: '', by: '' },
        description: { en: '', ru: '', by: '' },
        image: '',
        source: '',
      };

      this.events.push(event);
    },
    onSave() {
      const filename = this.title.ru;
      const file = new Blob([this.events], { type: "text/javascript" });
      if (window.navigator.msSaveOrOpenBlob)
          window.navigator.msSaveOrOpenBlob(file, "${filename}.js");
      else {
          const a = document.createElement("a");
          const url = URL.createObjectURL(file);
          a.href = url;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          setTimeout(() => {
              document.body.removeChild(a);
              window.URL.revokeObjectURL(url);
          }, 0);
      }
    },
    adjustTextarea(event) {
      const el = event.target;
      autosize(el);
    },
  },
};

</script>

<style scoped>

.event {
  border-top: 3px solid #c7c7c7;
}

.event:first-child {
  border-top: none;
}

.form-style-8 {
  font-family: 'Open Sans Condensed', arial, sans;
  width: 450px;
  padding: 30px;
  background: #FFFFFF;
  margin: 50px auto;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
  -moz-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
  -webkit-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
}

.form-style-8 h2 {
  background: #4D4D4D;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed', sans-serif;
  color: #797979;
  font-size: 18px;
  font-weight: 100;
  padding: 20px;
  margin: -30px -30px 30px -30px;
}

.form-style-8 input[type="text"],
.form-style-8 input[type="date"],
.form-style-8 input[type="datetime"],
.form-style-8 input[type="email"],
.form-style-8 input[type="number"],
.form-style-8 input[type="search"],
.form-style-8 input[type="time"],
.form-style-8 input[type="url"],
.form-style-8 input[type="password"],
.form-style-8 textarea,
.form-style-8 select {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  outline: none;
  display: block;
  width: 100%;
  padding: 7px;
  border: none;
  border-bottom: 1px solid #ddd;
  background: transparent;
  margin-bottom: 10px;
  font: 16px Arial, Helvetica, sans-serif;
  height: 45px;
}

.form-style-8 *:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.form-style-8 textarea {
  resize: none;
  overflow: hidden;
}

.form-style-8 input[type="button"],
.form-style-8 input[type="submit"] {
  -moz-box-shadow: inset 0px 1px 0px 0px #45D6D6;
  -webkit-box-shadow: inset 0px 1px 0px 0px #45D6D6;
  box-shadow: inset 0px 1px 0px 0px #45D6D6;
  background-color: #2CBBBB;
  border: 1px solid #27A0A0;
  display: inline-block;
  cursor: pointer;
  color: #FFFFFF;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 14px;
  padding: 8px 18px;
  text-decoration: none;
  text-transform: uppercase;
}

.form-style-8 input[type="button"]:hover,
.form-style-8 input[type="submit"]:hover {
  background: linear-gradient(to bottom, #34CACA 5%, #30C9C9 100%);
  background-color: #34CACA;
}

</style>



